import { Component, Prop, h } from '@stencil/core';
import { isExternalUrl } from '../../utils';
import { HINT_TYPES } from './exports';

@Component({
  tag: 'mdx-hint',
  styleUrl: 'hint.scss',
  shadow: true,
})
export class Hint {
  @Prop({ reflect: true })
  hint: string;

  @Prop({ reflect: true })
  type: (typeof HINT_TYPES)[keyof typeof HINT_TYPES] = HINT_TYPES.NEUTRAL;

  @Prop({ reflect: true })
  border: false;

  @Prop()
  href: string;

  @Prop()
  target: '_blank' | '_self' | '_parent' | '_top' = '_self';

  @Prop()
  linkText: string;

  private isExternalURL() {
    return isExternalUrl(this.href);
  }

  render() {
    return (
      <div class="hint">
        <div class="hint__container">
          <div class="hint__container-content">
            <slot name="icon" />
            {this.hint && <span>{this.hint}</span>}
          </div>
          {this.linkText && (
            <a
              class="hint__link"
              href={this.href}
              target={this.target}
              rel={this.isExternalURL() ? 'noreferrer' : undefined}
            >
              {this.linkText}
            </a>
          )}
        </div>
      </div>
    );
  }
}
