:host .hint {
  display: flex;
  align-items: center;
  font: var(--mdx-comp-hint-font-default);
  min-height: var(--mdx-comp-hint-sizing-container-height);
  width: var(--mdx-comp-hint-sizing-container-width);
  border-radius: var(--mdx-comp-hint-border-radius-default);

  @media screen and (max-width: 768px) {
    width: var(--mdx-comp-hint-sizing-container-mobile-width);
  }

  &__container {
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: var(--mdx-comp-hint-gap-icon-text);
    padding: var(--mdx-comp-hint-padding-vertical) var(--mdx-comp-hint-padding-horizontal);

    &-content {
      display: flex;
      align-items: center;
      gap: var(--mdx-comp-hint-gap-icon-text);
      hyphens: auto;
    }

    ::slotted([slot='icon']) {
      height: var(--mdx-comp-hint-sizing-icon);
      width: var(--mdx-comp-hint-sizing-icon);
      color: var(--mdx-comp-hint-transparent-icon-color-default);
    }
  }

  &__link {
    font: var(--mdx-comp-hint-font-link);
    text-decoration: var(--mdx-comp-hint-decoration-link);
    white-space: nowrap;

    &:focus-visible {
      border-radius: var(--mdx-comp-keyboard-focus-border-radius-default);
      outline-color: var(--mdx-comp-keyboard-focus-color-default);
      outline-width: var(--mdx-comp-keyboard-focus-border-width-default);
      outline-style: solid;
    }
  }
}

:host([type='neutral'][border]) .hint {
  border-color: var(--mdx-comp-hint-neutral-border-color-default);
  border-width: var(--mdx-comp-hint-border-default);
  border-style: solid;
  background-color: var(--mdx-comp-hint-neutral-background-color-default);

  &__container {
    color: var(--mdx-comp-hint-neutral-label-color-default);

    ::slotted([slot='icon']) {
      color: var(--mdx-comp-hint-neutral-icon-color-default);
    }
  }
}

